.OurCoffeeAbout {
    position: relative;
    height: 450px;
    padding: 70px 0 15px 0;
    h3 {
        margin: 0;
        margin-top: 9px;
        font-size: 24px;
    }
    &__block {
       
        display: flex;
        align-items: start;
        /* justify-content: space-between */
    }
    &__img {
        display: block;
        margin-right: 79px;
    }
    &__items {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img {
            margin-top: 20px;
            display: block;
        }
    }
    p{
        margin: 0;
        margin-top: 25px;
        white-space: pre-line;
        text-align: center;
    /*     max-width: 346px; */
    }

    &__border {
        position: absolute;
        top: 485px;
        left: 50%;
        transform: translateX(-50%);
        width: 240px;
        height: 1px;
        background-color: black;
    }
   
}